<template>
  <div class="login-container">
    <el-card class="box-card" shadow="hover">
      <el-form ref="form" label-width="70px" :model="form" :rules="rules">
        <div class="form-header">系统登陆</div>
        <el-form-item label="用户名" prop="username">
          <el-input v-model="form.username" placeholder="请输入账号"></el-input>
        </el-form-item>
        <el-form-item label="密码" prop="password">
            <!-- show-password属性可以调整密码是否可见 -->
          <el-input
            show-password
            v-model="form.password"
            placeholder="请输入密码"
          ></el-input>
        </el-form-item>
        <el-form-item class="form-item-button">
          <el-button type="primary" @click="submit" >登陆</el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>
import Cookie  from "js-cookie";
import {getMenu} from "../api";
export default {
  name: "MyAppHun",
  data() {
    return {
      form: {
        username: "",
        password: "",
      },
      rules: {
        username: [
          { required: true, message: "请输入用户名", trigger: "blur" },
        ],
        password: [{ required: true, message: "请输入密码", trigger: "blur" }],
      },
    };
  },

  mounted() {},

  methods: {
    // 登陆
    submit() {
        //对登录信息进行校验
        this.$refs.form.validate((valid) => {
            if (valid) {
                // 调用登录接口,解构出token（token即保存的用户信息）
                getMenu(this.form).then(({ data }) => {
                    console.log(data);
                    // 保存token到本地存储
                    if(data.code === 20000)
                    {
                        //将token信息存入Cookie用语不同页面间的通信
                        Cookie.set('token',data.data.token)

                        //获取菜单的数据，存入store中(若启用了命名空间，你需要使用模块的名称来提交 mutation)
                        this.$store.commit('tab/SET_MENU',data.data.menu)
                        this.$store.commit('tab/ADD_MENU',this.$router)
                        // 跳转到首页
                        this.$router.push('/home');
                    }
                    else {
                    this.$message.error(data.data.message);
                    }
                });
            } 
        });
    },    
}
};
</script>
<style lang="less" scoped>
.login-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  .box-card {
    width: 400px;
    border-radius: 15px;
    text-align: center;
    .form-header {
      font-size: 24px;
      margin-bottom: 20px;
    }
    /deep/ .form-item-button {
        .el-form-item__content{
            margin-left: 0 !important;
        }
    }
  }
}
</style>
