<template>
  <el-menu
    :default-active="activeMenu"
    class="el-menu-vertical-demo"
    @open="handleOpen"
    @close="handleClose"
    :collapse="isCollapse"
    background-color="#545c64"
    text-color="#fff"
    active-text-color="#ffd04b"
  >
    <h3>{{ isCollapse ? "后台" : "通用后台管理系统" }}</h3>
    <router-link
      v-for="item in noChildrenMenu"
      :key="item.name"
      :to="item.path"
      
    >
      <el-menu-item :index="item.name" @click="clickMenu(item)">
        <i :class="`el-icon-${item.icon}`"></i>
        <span slot="title">{{ item.label }}</span>
      </el-menu-item>
    </router-link>
    <el-submenu
      v-for="item in hasChildrenMenu"
      :key="item.name"
      :index="item.name"
    >
      <template slot="title">
        <i :class="`el-icon-${item.icon}`"></i>
        <span slot="title">{{ item.label }}</span>
      </template>
      <router-link
        v-for="subItem in item.children"
        :key="subItem.name"
        :to="subItem.path"
        
      >
        <el-menu-item-group>
          <el-menu-item :index="subItem.name" @click="clickMenu(subItem)">{{ subItem.label }}</el-menu-item>
        </el-menu-item-group>
      </router-link>
    </el-submenu>
  </el-menu>
</template>

<script>
import { mapState } from "vuex";
import Cookie from "js-cookie";

export default {
  data() {
    return {
      //   isCollapse: false,
    };
  },
  methods: {
    handleOpen(key, keyPath) {
      console.log(key, keyPath);
    },
    handleClose(key, keyPath) {
      console.log(key, keyPath);
    },
    clickMenu(item) {
      this.$store.commit('tab/SELECT_MENU', item)
    }
  },
  computed: {
    noChildrenMenu() {
      return this.menuData.filter((item) => !item.children);
    },
    hasChildrenMenu() {
      return this.menuData.filter((item) => item.children);
    },
    menuData(){
      return JSON.parse(Cookie.get('menu'))
    },
    ...mapState("tab", ["isCollapse","activeMenu"]),// 映射isCollapse和activeMenu 状态
    // ...mapMutations("tab", {clickMenu: "UPDATE_TAB_LIST"}),

  },
};
</script>

<style lang="less" scoped>
.el-menu-vertical-demo:not(.el-menu--collapse) {
  width: 200px;
  min-height: 100vh;
}
.el-menu {
  border-right: none;
  height: 100%;
  h3 {
    color: #fff;
    line-height: 48px;
    text-align: center;
  }
}
</style>
