<template>
  <div class="header-container">
    <div class="l-content">
      <el-button
        @click="changeMenu"
        icon="el-icon-menu"
        size="mini"
        style="margin-right: 20px"
      ></el-button>
      <!-- <span class="text">首页</span> -->
      <el-breadcrumb separator="/">
        <el-breadcrumb-item
          v-for="item in tabList"
          :key="item.path"
          :to="
            item.path !== $route.path &&
            !($route.path === '/home' && item.path === '/')
              ? { path: item.path }
              : null
          " @click.native="clickBreadcrumb(item)"
          >{{ item.label }}
        </el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="r-content">
      <el-dropdown >
        <span class="el-dropdown-link">
          <img src="../assets/images/user.png" alt="" class="user-img" />
        </span>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item>个人中心</el-dropdown-item>
          <el-dropdown-item @click.native="logout">退出</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
    </div>
  </div>
</template>

<script>
import Cookie from "js-cookie";
import { mapMutations, mapState } from "vuex";

export default {
  name: "MyAppCommonHeader",

  data() {
    return {};
  },

  mounted() {},

  methods: {
    ...mapMutations("tab", { changeMenu: "COLLAPSE_MENU", selectMenu: "SELECT_MENU" }),
    clickBreadcrumb(item) {
      this.selectMenu(item); // 更新 Vuex 状态
    },
    // 退出登录
    logout() {
      //清楚Cookie中的token
      Cookie.remove("token");
      //清除Cookie中的menu
      Cookie.remove("menu");
      //跳转登录页面
      this.$router.push("/login");
    }
  },
  computed: {
    ...mapState("tab", ["tabList"]),
  },
};
</script>

<style lang="less" scoped>
.header-container {
  padding: 0 20px;
  background-color: #333;
  height: 60px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  .text {
    font-size: 14px;
    color: #fff;
    margin-left: 10px;
  }
  .l-content {
    display: flex;
    align-items: center;
    .el-button {
      margin-right: 10px;
    }
  }
  .r-content {
    .user-img {
      width: 40px;
      height: 40px;
      border-radius: 50%;
    }
  }
}
</style>
