import Cookie from 'js-cookie'
export default {
  namespaced: true,
  state: {
    isCollapse: false, //用于控制左侧菜单栏的展开收起状态，此时默认为展开状态
    tabList: [
      {
        path: "/",
        name: "home",
        label: "首页",
        icon: "s-home",
        url: "Home/Home",
      },
    ], //面包屑数据
    menu: [], // 菜单数据
    activeMenu: 'home' // 新增状态，用于跟踪当前激活的菜单项
  },
  mutations: {
    //修改左侧菜单栏的展开收起状态
    COLLAPSE_MENU(state) {
      state.isCollapse = !state.isCollapse;
    },
    //更新面包屑数据
    SELECT_MENU(state, value) {
      // console.log('hahahaha');
        state.activeMenu = value.name; // 更新当前激活的菜单项
      //判断添加的数据是否为首页
      if (value.name !== "home") {
        const index = state.tabList.findIndex(
          (item) => item.name === value.name
        );
        if (index === -1) {
          state.tabList.push(value);
        }
      }
    },
    //删除指定的tag数据
    CLOSE_TAG(state, index) {
      state.tabList.splice(index, 1);
    },
    //设置menu的数据
    SET_MENU(state, value) {
      state.menu = value;
      //由于浏览刷新就会导致menu数据丢失，所以需要将menu数据存入cookie
      Cookie.set('menu', JSON.stringify(value))
    },
    //动态注册路由
    ADD_MENU(state, router){
      //判断缓存中是否有数据
      if(!Cookie.get('menu')) return
      const menu = JSON.parse(Cookie.get('menu'));
      state.menu = menu;
      //组装动态路由的数据
      const menuArray = [];
      menu.forEach(item => {
        if(item.children){
          item.children = item.children.map(item => {
            item.component= () => import(`../views/${item.url}`)
            return item
          })
          menuArray.push(...item.children)
          }else{
            item.component= () => import(`../views/${item.url}`)
            menuArray.push(item)
          }
        })
        //路由的动态添加
        menuArray.forEach(item => {
          router.addRoute('Main', item)
        })
    }
}
}
