<template>
  <div class="tags">
    <el-tag
      v-for="(item, index) in tabList"
      :key="item.path"
      :closable="item.name !== 'home'"
      :effect="$route.name === item.name ? 'dark' : 'plain'"
      @click="changeMenu(item)"
      @click.native="clickTag(item)"
      @close="handleClose(item, index)"
      size="small"
    >
      {{ item.label }}
    </el-tag>
  </div>
</template>

<script>
import { mapState, mapMutations } from "vuex";
export default {
  name: "MyAppCommonTag",

  data() {
    return {};
  },
  computed: {
    ...mapState("tab", ["tabList"]),
  },
  methods: {
    ...mapMutations( "tab",['CLOSE_TAG']),
    ...mapMutations("tab",{selectMenu: "SELECT_MENU"}),
    
    //// 更新 Vuex 状态,点击tag实时同步更新左侧导航栏激活的菜单项
    clickTag(item)
    {
      this.selectMenu(item);
    },
    //点击tag跳转的功能
    changeMenu(item) {  
      //解决双击tag报错的问题
      if( item.path !== this.$route.path && !(this.$route.path === '/home' && item.path === '/'))
      {
        this.$router.push(item.path);
      }
      else{
        return;
      }
    },
    //点击tag删除的功能
    handleClose(item, index) {
      //使用store中的mutations方法关闭tag
      this.CLOSE_TAG(index);
      //删除tag后，跳转到前一个tag界面
      if (index === this.tabList.length - 1) {
        this.$router.push(this.tabList[index - 1].path);
        return;//执行完成后终止当前函数进程
      }

      // 删除当前活跃页面时
      if (this.$route.name === item.name) {
        this.$router.push(this.tabList[index - 1].path);
      }
    },
    mounted() {
    },
  },
};
</script>

<style lang="less" scoped>
.tags {
  padding: 20px;
  .el-tag {
    margin-right: 10px;
    cursor: pointer;
  }
}
</style>
